import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import StackGrid from 'react-stack-grid';
import { Tabs, Tab } from 'material-ui/Tabs';
import PostDetail from '../container/post-detail';
import Post from '../container/post';



const styles = {
    tabs: {

    },
    tab: {
        backgroundColor: '#fff'
    },
    tabBtn: {
        color: 'black'
    },
    inkBar: {
        backgroundColor: '#00bcd4'
    },
    stackGrid: {
        width: '100%',
        marginTop: 20
    },
    item: {
        width: '100%'
    }
};

const now = new Date();

const masonryOptions = {
    transitionDuration: 0
};

class PostList extends Component {

    constructor(props) {
        super(props);
        this.state = {
            value: 'a', width: '0', height: '0', colWidth: '50%'
        };

        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
        this.resetStack = this.resetStack.bind(this);

    }

    handleChange = (value) => {
        this.setState({
            value: value,
        });
    };
    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });

        if (this.state.width <= 768 || window.innerWidth <= 768) {
            this.setState({ colWidth: '100%' });
        } else {
            this.setState({ colWidth: '50%' });
        }

        if (this.state.width == 0 && window.innerWidth > 768) {
            this.setState({ colWidth: '50%' });
        } else if (this.state.width == 0 && window.innerWidth < 768) {
            this.setState({ colWidth: '100%' });
        }
        
        //reset after resize window
        this.setState({ colWidth: this.state.colWidth });
    }

    renderPost(post) { 
        let postList = [];
        if (post.length > 0) { 
            postList = post[0].results.map((post) => {
                return (<div key={post.id} > <Post data={post} /> </div>);
            });

        }

        return postList;
    }

    resetStack() {
        this.setState({ colWidth: this.state.colWidth });

        // this.setState({ colWidth: '51%' });
        // setTimeout(() => {
        //     this.setState({ colWidth: '50%' });
        // }, 10);

    }

    render() {

        return (
            <div >

                <ul id="postType" className="nav nav-tabs">
                    <li className="active"><a data-toggle="tab" href="#home">Mới nhất</a></li>
                    <li><a data-toggle="tab" href="#menu1">Like nhiều</a></li>
                    <li><a data-toggle="tab" href="#menu2">Xu hướng</a></li>
                </ul>

                <div className="tab-content">
                    <div id="home" className="tab-pane fade in active">
                        <StackGrid
                            style={styles.stackGrid}
                            columnWidth={this.state.colWidth}
                            gutterWidth={25}
                            gutterHeight={15}
                            duration={0}
                        >
                            <div style={styles.item} key="A"> <PostDetail resetStack={this.resetStack} /></div>

                            {this.renderPost(this.props.latestPost)}


                        </StackGrid>
                    </div>
                    <div id="menu1" className="tab-pane fade">
                        <h3>Menu 1</h3>
                        <p>Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>
                    </div>
                    <div id="menu2" className="tab-pane fade">
                        <h3>Menu 2</h3>
                        <p>Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam.</p>
                    </div>

                </div>
            </div>
        );
 
    }
}

function mapStateToProps(state) { 
    return { latestPost: state.latestPost };

}


export default connect(mapStateToProps)(PostList);


