import React, { Component } from 'react';
import { Link, withRouter } from 'react-router';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { fetchPost } from '../actions/index';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';
import IconButton from 'material-ui/IconButton';
import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';
import { Toolbar, ToolbarGroup, ToolbarSeparator, ToolbarTitle } from 'material-ui/Toolbar';
import StackGrid from "react-stack-grid";

import PostDetail from '../container/post-detail';
  

class IconRightAppBar extends Component {
  render() {
    return (
      <ToolbarGroup>
        <div>
          <FlatButton label="Trang chủ" containerElement={<Link to="/">Trang chủ</Link>} />
          <FlatButton label="Chi tiết" containerElement={<Link to="/detail">Chi tiết</Link>} />
        </div>
      </ToolbarGroup>
    );
  }

}

const Logged = (props) => (
  <IconMenu
    {...props}
    iconButtonElement={
      <IconButton><MoreVertIcon /></IconButton>
    }
    targetOrigin={{ horizontal: 'right', vertical: 'top' }}
    anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
  >
    <Link className='popOverItem' to="/"><MenuItem primaryText="Trang Chủ" /></Link>
    <Link className='popOverItem' to="/detail"><MenuItem primaryText="Chi Tiết" /></Link>
  </IconMenu>
);

class App extends Component {

  constructor(props) {
    super(props);
    this.state = { openDrawer: false, open: false };
    this.handleToggle = this.handleToggle.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);

    this.handleAppBarTitleTap = this.handleAppBarTitleTap.bind(this);

    this.props.fetchPost();

  }
  componentDidMount() {
    document.addEventListener('mousedown', this.handleClickOutside);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handleClickOutside);
  }

  handleToggle() {
    this.setState({ openDrawer: !this.state.openDrawer });

  }

  handleClickOutside() {
    this.setState({ openDrawer: false });
  }


  handleAppBarTitleTap() {
    this.props.router.push("/");
  }

  render() {
    return (
      <div>

        <div className="container-fluid gridFluid"   >
          <div className="row">

            <MuiThemeProvider>
              <AppBar
                title={<span className='title' >Title</span>}
                onLeftIconButtonTouchTap={this.handleToggle}
                className='appBar'
                iconElementLeft={<Logged />}
                onTitleTouchTap={this.handleAppBarTitleTap}

              />

            </MuiThemeProvider>

          </div>

          <div className="container-fluid">

            <div className="col-md-2" >
              <MuiThemeProvider>
                <div className='sideBar'  >
                </div>

              </MuiThemeProvider>
            </div>
            <div className="col-md-8" >
              <MuiThemeProvider className='midCol'   >
                {this.props.children}
              </MuiThemeProvider>
            </div>
            <div className="col-md-2" >
              <MuiThemeProvider>
                <div className='sideBar'  >

                </div>

              </MuiThemeProvider>
            </div>




          </div>
        </div>

      </div>
    );
  }
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({ fetchPost }, dispatch);
}

export default connect(null, mapDispatchToProps)(withRouter(App));




