import React, { Component } from 'react';
 import Moment from 'moment';

import { Card, CardActions, CardHeader, CardMedia, CardTitle, CardText } from 'material-ui/Card';
import IconButton from 'material-ui/IconButton';
import ThumbUp from 'material-ui/svg-icons/action/thumb-up';
import ThumbDown from 'material-ui/svg-icons/action/thumb-down';
import Share from 'material-ui/svg-icons/action/settings';
import Send from 'material-ui/svg-icons/action/camera-enhance';


const styles = {
  smallIcon: {
    width: 19,
    height: 19,
    color: 'rgba(193, 172, 172, 0.87)'
  },
  small: {
    width: 25,
    height: 25,
    padding: 0,
    marginRight: 5
  },
  action: {
  },
  actionWrapper: {
    display: 'inline'
  },
  content: {
    fontSize: 20,
    fontWeight: 700
  }
};



export default class Post extends Component {

  constructor(props) {
    super(props);
    //console.log(props);
    this.state = { createdDate: Moment.unix(this.props.data.created_timestamp).format("HH:mm DD/MM/YYYY") };

  }

  render() {

    return (
      <div>
        <Card >
          <CardHeader
            title={this.props.data.owner.username}
            subtitle={this.state.createdDate}
            avatar="https://lh3.googleusercontent.com/kH3Jdcjw2gWoM-101_FW49CIzdff93rmCKKMY22fKusqbqdjBusLn2WWU4RQqEmH9z4Tabb720ZJC64oL874yeCx0BhRV_rKwgAyBzls7Hhnei4t1SDWatMQiPBzcW8RCkxPXp9g62Xzptf1JyLHZ5I48N3cCAkVXcHJIrD26fWr41R7bX_kWi3dkU_k-Uj25vzcRtyMTzHWAAwbZtepMZXjTMajGIMNLnRhTYjZS7n0LB4cCnuNJJTiBSBazh6RdZmamfcvBdMpAJcM_hLzQleDwatxbbNosYn2dwR9Sem7GQGPR1nAb7mnekbLcSx5Pt0EAM429Xms-qNPkYBjgwVzFYD-n2jfwpKI-jYonrwbLfXa5s0IsrcGQsuPAshS3mCLdsPCmiS-UB3xR10QJg5dPAA97T6TmI57MFV296Yolh8k9YRMj9K25apB3vzNl8D1wJOmb67devp4PG6z2ve-SK222EVV7xo-eV1rR-i2G-onRFtMKSA7TZWBpcIKrcsvCKgjxHL8ZPRmwkF8Z4A7mXopg89agfxGDQgvoVwBujQLOykqvT5RjuqwDI2wbuekOs5z_9CT_hU2AK-xMAEWnv7Y9F6AyiXvsjTmLw=s524-no"
          />
          {/* <CardTitle title="Card title" subtitle="Card subtitle" /> */}
          <CardText className='wordwrap' >
            <p>{this.props.data.content}   </p>

          </CardText>
          <CardMedia
        //  overlay=   {this.props.data.photos.length>1?<CardTitle title='còn nhiều hình nữa' />:<CardTitle title='1 tấm' />}  
          >


            {
              (this.props.data.video_urls.length > 0) ?
                <iframe style={{ width: '100%' }} height="315" src={'//www.youtube.com/embed/' + this.props.data.video_urls[0]} frameBorder="0" allowFullScreen></iframe>
                : (this.props.data.photos.length>0) ? <img height="315" src={ this.props.data.photos[0].url.replace('{size_width}','0')} alt=""  />:null
            }
 
            {/* <p> {JSON.stringify(this.props.data)} </p> */}
          </CardMedia>
          <CardActions style={styles.actions} >
            <div style={styles.actionWrapper} >
              <IconButton
                iconStyle={styles.smallIcon}
                style={styles.small}
              >
                <ThumbUp style={styles.action} />

              </IconButton>
              <span>{this.props.data.like_count}</span>
            </div>
            <div style={styles.actionWrapper} >
              <IconButton
                iconStyle={styles.smallIcon}
                style={styles.small}
              >
                <ThumbDown style={styles.action} />
              </IconButton>
              <span>{this.props.data.dislike_count}</span>
            </div>

            <div style={styles.actionWrapper} >
              <IconButton
                iconStyle={styles.smallIcon}
                style={styles.small}
              >
                <Send style={styles.action} />
              </IconButton>
            </div>

          </CardActions>
        </Card>

      </div>

    );
  }
}



