import { FETCH_LATEST } from '../actions/index';


export default function (state = [], action) {

    switch (action.type) {
        case FETCH_LATEST: {
            //console.log('RECEIVED:',action.payload.data.data);
            return [action.payload.data.data, ...state];
        }
         

    }

    return state;
}


