import axios from 'axios';
import { expoIn } from 'react-stack-grid/lib/animations/easings';
import { connect } from 'react-redux';

const ROOT_URL = 'https://devchanel.herokuapp.com';

//-----------------------------------------------
export const FETCH_LATEST = 'FETCH_LATEST';

export function fetchPost(city) {
    let path = '/api/v1/posts';

    const url = `${ROOT_URL}${path}?limit=1000`;
    //console.log(url);
    const request = axios.get(url);



    return {
        type: FETCH_LATEST,
        payload: request
    };
}

//-----------------------------------------------
export const AUTH_USER = 'AUTH_USER';
export const UNAUTH_USER = 'UNAUTH_USER';

export function signinUser({ username, password }) {
    let path = '/api/v1/auth/token/';

    return function (dispatch) {
        //submit to server
        axios.post(`${ROOT_URL}${path}`, { username, password })
            .then(res => {

                if (res.data.status.status === 200) {
                    dispatch({ type: AUTH_USER });
                    //save token to localStorage
                    localStorage.setItem('token', res.data.data.token);

                } else {
                    dispatch({ type: UNAUTH_USER });
                }

            })
            .catch(ex => {
                dispatch({ type: UNAUTH_USER });
            });

    }
}

export function registerUser({ username, email, password }) {
    let path = '/api/v1/auth/register/';

    return function (dispatch) {
        //submit to server
        axios.post(`${ROOT_URL}${path}`, { username, email, password })
            .then(res => {

                if (res.data.status.status === 200) {
                    dispatch({ type: AUTH_USER });
                    //save token to localStorage
                    localStorage.setItem('token', res.data.data.token);

                } else {
                    dispatch({ type: UNAUTH_USER });
                }

            })
            .catch(ex => {
                dispatch({ type: UNAUTH_USER });
            });

    }
}


export function uploadImage({ src, file }) {
    let path = '/api/v1/drive/upload-photo/';

    var formData = new FormData();
    formData.append("image", file);


    return function (dispatch) {
        //submit to server
        axios.post(`${ROOT_URL}${path}`, formData
            , { headers: { 'Content-Type': 'multipart/form-data', Authorization: 'Token ' + localStorage.getItem('token') } })

            .then(res => {

                if (res.data.status.status === 200) {
                    console.log('upload image success');
                } else {
                    console.log('upload fail: ' + res.data.status.status);
                }

            })
            .catch(ex => {
                console.log('upload fail: ' + ex);
            });

    }
}

export function uploadPost({ content, images },waitScreen) {
    let pathPhoto = '/api/v1/drive/upload-photo/';
    let pathPost = '/api/v1/posts/new/';
    let promises = [];
    let imageIdArr = [];

    return function (dispatch) {
        waitScreen(true);
        if (images != null && images.length != 0) {
            console.log('postWithImage');
            //wait all promise done and post
            postWithImage(waitScreen);

        } else {
            console.log('postWithoutImage');
            //submit post
            postWithoutImage(waitScreen);

        }
 
    }

    function postWithoutImage(waitScreen) {
        axios.post(`${ROOT_URL}${pathPost}`, { photos: imageIdArr, content: content }, { headers: { 'Content-Type': 'application/json', Authorization: 'Token ' + localStorage.getItem('token') } })
            .then(res => {
                waitScreen(false);
                console.log(res);
                if (res.data.status.status === 200) {
                  //  alert('Đăng bài thành công.');
                  //  console.log(res);
                }
                else {
                    console.log('upload fail: ' + res.data.status.status);
                }
            })
            .catch(ex => {
                waitScreen(false);
                console.log('upload fail: ' + ex);
            });
    }

    function postWithImage(waitScreen) {
        images.forEach(function (image) {
            var formData = new FormData();
            formData.append("image", image.file);
            promises.push(axios.post(`${ROOT_URL}${pathPhoto}`, formData, { headers: { 'Content-Type': 'multipart/form-data', Authorization: 'Token ' + localStorage.getItem('token') } }));
        });
        //post all images and get id
        axios.all(promises).then(function (results) {
            results.forEach(function (res) {
                imageIdArr.push(res.data.data.id);
            });
            //submit post to server
            axios.post(`${ROOT_URL}${pathPost}`, { photos: imageIdArr, content: content }, { headers: { 'Content-Type': 'application/json', Authorization: 'Token ' + localStorage.getItem('token') } })
                .then(res => {
                    waitScreen(false);
                    console.log(res);
                    if (res.data.status.status === 200) {
                      //  alert('Đăng bài thành công.');
                    }
                    else {
                        console.log('upload fail: ' + res.data.status.status);
                    }
                })
                .catch(ex => {
                    waitScreen(false);
                    console.log('upload fail: ' + ex);
                });
        });
    }
}


