import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import FlatButton from 'material-ui/FlatButton';


import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { registerUser } from '../actions/index';

class RegisterForm extends Component {
    constructor(state) {

        super(state);
        this.state = {
            formInvalid: false,
            errorText: null
        };

        this.handleSubmit = this.handleSubmit.bind(this);
        this.openLoginForm = this.openLoginForm.bind(this);
    }

    handleSubmit() {
        let username = this.refs.username.getValue();
        let email = this.refs.email.getValue();
        let password = this.refs.password.getValue();
        let confirm = this.refs.confirmPassword.getValue();

        if (username != '' && email != '' && password != '' && confirm != '') {
            if (password === confirm) {
                this.props.registerUser({ username, email, password });
            } else {
                alert("Xác nhận mật khẩu không đúng.");
            }
        } else {
            this.setState({ errorText: 'trường bắt buộc' });
        }



    }

    openLoginForm() {
        this.props.openLoginForm();
    }

    render() {
        return (
            <form>
                <TextField
                    fullWidth={true}
                    ref='username'
                    errorText={this.state.errorText}
                    hintText="Username"
                    multiLine={false}
                    rows={1}

                /><br />
                <TextField
                    fullWidth={true}
                    ref='email'
                    errorText={this.state.errorText}
                    hintText="Email"
                    multiLine={false}
                    rows={1}

                /><br />
                <TextField
                    fullWidth={true}
                    ref='password'
                    errorText={this.state.errorText}
                    hintText="Password"
                    multiLine={false}
                    rows={1}
                    type='password'
                /><br />
                <TextField
                    fullWidth={true}
                    ref='confirmPassword'
                    errorText={this.state.errorText}
                    hintText="Confirm password"
                    multiLine={false}
                    rows={1}
                    type='password'
                /><br />
                <div  >
                    <FlatButton
                        label="Đăng nhập"
                        primary={true}
                        disabled={this.state.formInvalid}
                        onClick={this.openLoginForm}
                        className='loginBtn'
                    />
                    <FlatButton
                        label="Đăng ký"
                        primary={true}
                        disabled={this.state.formInvalid}
                        onClick={this.handleSubmit}
                        className='regisAction'
                    />
                    <FlatButton
                        label="Hủy bỏ"
                        primary={true}
                        onClick={this.props.handleCloseDialog}
                        className='regisAction'
                    />
                </div>

            </form>
        );
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({ registerUser }, dispatch);
}

export default connect(null, mapDispatchToProps)(RegisterForm);


