import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import FlatButton from 'material-ui/FlatButton';

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { signinUser } from '../actions/index';
 
class LoginForm extends Component {
    constructor(state) {

        super(state);
        this.state = {
            formInvalid: false, 
            errorTextUserName:null,
            errorTextPassword:null
        };

        this.handleSubmit = this.handleSubmit.bind(this);
        this.openRegisForm = this.openRegisForm.bind(this);

    }

    handleSubmit() {
        let username = this.refs.username.getValue();
        let password = this.refs.password.getValue();

        if (username != '' && password != '') {
            this.props.signinUser({ username, password });
        }else{
           this.setState({errorTextUserName:'trường bắt buộc',errorTextPassword:'trường bắt buộc'});
        }
 
    }

    openRegisForm() {
        this.props.openRegisForm();
        
    }


    render() {


        return (
            <form>
                <TextField
                    fullWidth={true}
                    ref='username'
                    errorText={this.state.errorTextUserName}
                    hintText="Username"
                    multiLine={false}
                    rows={1}

                /><br />

                <TextField
                    fullWidth={true}
                    ref='password'
                    errorText={this.state.errorTextPassword}
                    hintText="Password"
                    multiLine={false}
                    rows={1}
                    type='password'
                /><br />

                <div  >
                    <FlatButton
                        label="Đăng Ký"
                        primary={true}
                        disabled={this.state.formInvalid}
                        onClick={this.openRegisForm}
                        className='regisBtn'
                    />
                    <FlatButton
                        label="Đăng Nhập"
                        primary={true}
                        disabled={this.state.formInvalid}
                        onClick={this.handleSubmit}
                        className='loginAction'
                    />
                    <FlatButton
                        label="Hủy bỏ"
                        primary={true}
                        onClick={this.props.handleCloseDialog}
                        className='loginAction'
                    />
                </div>

            </form>
        );
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({ signinUser }, dispatch);
}

export default connect(null, mapDispatchToProps)(LoginForm);


