import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { uploadPost } from '../actions/index';

import TextField from 'material-ui/TextField';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';
import { Card, CardActions, CardHeader, CardMedia, CardTitle, CardText } from 'material-ui/Card';
import Snackbar from 'material-ui/Snackbar';
import Paper from 'material-ui/Paper';
import Dialog from 'material-ui/Dialog';
import Avatar from 'material-ui/Avatar';
import Send from 'material-ui/svg-icons/action/camera-enhance';

import RegisterForm from '../components/register-form';
import LoginForm from '../components/login-form';



const styles = {
    hide: {
        display: 'none'
    },
    show: {
        display: 'block'
    },

    snackBarContent: {
        color: 'rgb(255, 64, 129)',
        paddingLeft: 16,
        paddingRight: 16,
        verticalAlign: 'middle',
        letterSpacing: 0,
        textTransform: 'uppercase',
        fontWeight: 500,
        fontSize: 14
    }
}


class PostDetail extends Component {
    constructor(state) {

        super(state);
        this.state = {
            errorText: null,
            imageSrc: [],
            imageName: null,
            alert: false,
            alertMess:' ',
            content: null,
            helpText: null,
            openRegisForm: false,
            displayRegis: false,
            youtubePreview: null
        };
        this.handleContentChange = this.handleContentChange.bind(this);
        this.handleUploadImage = this.handleUploadImage.bind(this);
        this.handleRequestCloseSnackBar = this.handleRequestCloseSnackBar.bind(this);
        this.submitForm = this.submitForm.bind(this);
        this.handleCloseDialog = this.handleCloseDialog.bind(this);
        this.openRegisForm = this.openRegisForm.bind(this);
        this.openLoginForm = this.openLoginForm.bind(this);
        this.renderImageAvatar = this.renderImageAvatar.bind(this);
        this.deleteImageAvatar = this.deleteImageAvatar.bind(this);
        this.waitScreen=this.waitScreen.bind(this);
    }

    componentWillReceiveProps(nextProps) {
        //check login success
        if (nextProps.auth !== this.props.auth) {
            if (nextProps.auth.authenticated == true) {
                this.handleCloseDialog();
            } else {
                alert("Đăng nhập thất bại.");
            }
        }
    }

    handleContentChange(e) {
        var obj = e.target; 
        let regEx= /#[a-zA-z0-9áàạảãâấầậẩẫăắằặẳẵÁÀẠẢÃÂẤẦẬẨẪĂẮẰẶẲẴéèẹẻẽêếềệểễÉÈẸẺẼÊẾỀỆỂỄóòọỏõôốồộổỗơớờợởỡÓÒỌỎÕÔỐỒỘỔỖƠỚỜỢỞỠúùụủũưứừựửữÚÙỤỦŨƯỨỪỰỬỮíìịỉĩÍÌỊỈĨđĐýỳỵỷỹÝỲỴỶỸ]+/mg;
 
        this.setState({
            helpText: 'Hashtag của bạn: ' + (obj.value.match(regEx) != null ? obj.value.match(regEx) : []).join(', '),
            content: obj.value
        }, () => this.props.resetStack());

        if (this.state.content != null && this.state.content.match(/http(?:s?):\/\/(?:www\.)?youtu(?:be\.com\/watch\?v=|\.be\/)([\w\-\_]*)(&(amp;)?‌​[\w\?‌​=]*)?\w+/mg)) {
            console.log(this.state.content.match(/http(?:s?):\/\/(?:www\.)?youtu(?:be\.com\/watch\?v=|\.be\/)([\w\-\_]*)(&(amp;)?‌​[\w\?‌​=]*)?\w+/mg));
            let first = this.state.content.match(/http(?:s?):\/\/(?:www\.)?youtu(?:be\.com\/watch\?v=|\.be\/)([\w\-\_]*)(&(amp;)?‌​[\w\?‌​=]*)?\w+/mg)[0];
            let videoId = first.match(/=(.)\w+/g)[0].replace('=', '');
            let youtubeLink = `//www.youtube.com/embed/${videoId}`;
            console.log(youtubeLink);
            this.setState({ youtubePreview: youtubeLink }, () => this.props.resetStack());

        } else {
            this.setState({ youtubePreview: null });
        }



        if (obj.value == '') {
            this.setState({
                helpText: 'Tất cả bài đăng yêu cầu ít nhất 1 hashtag'
            });
            this.setState({ errorText: "trường bắt buộc" });
        } else {
            this.setState({ errorText: null });
        }


    }

    handleUploadImage(e) {
        var obj = e.target;

        let reader = new FileReader();
        let file = obj.files[0];

        try {
            //console.log(file);
            if (file != null && file.type == 'image/jpg' || file.type == 'image/bmp' || file.type == 'image/jpeg' || file.type == 'image/png') {

                try {
                    reader.onloadend = () => {
                        let image = { src: reader.result, file: file };
                        var index = this.state.imageSrc.indexOf(image);

                        if (index < 0) {

                            this.setState({ imageSrc: [...this.state.imageSrc, image] }, () => this.props.resetStack());

                        }

                    }
                    reader.readAsDataURL(file);
                } catch (e) {
                    console.log(e.message);
                }
            } else {
                this.setState({ alert: true,alertMess:"file ảnh sai định dạng!" });
            }
        } catch(e) { }


    }

    handleRequestCloseSnackBar() {
        this.setState({ alert: false });
    }

    handleCloseDialog = () => {
        this.setState({ openRegisForm: false });
    };

    submitForm(e) {
        if (this.props.auth.authenticated != true && localStorage.getItem('token') == undefined) {
            //if not login, open register form
            this.setState({ openRegisForm: !this.state.openRegisForm });
        } else {
            if (this.state.content != null && this.state.content.trim() != '') {
                this.props.uploadPost({ content: this.state.content, images: this.state.imageSrc },this.waitScreen);

            } else {
                this.setState({ errorText: "trường bắt buộc" });
            }

        }

    }

    waitScreen(wait){
        if(wait){
            console.log('waiting...');
            this.setState({ alert: true,alertMess:"Đang tải lên" });
        }else{
            console.log('wait done');
            this.setState({ alert: true,alertMess:"Đăng bài thành công" });
        }
    }

    openRegisForm() {
        this.setState({ displayRegis: true });
    }

    openLoginForm() {
        this.setState({ displayRegis: false });
    }
    renderImageAvatar() {
        return this.state.imageSrc.map((avatar) => {
            return (<Avatar onClick={this.deleteImageAvatar.bind(this)} data-id={avatar.src} key={avatar.src} style={{ cursor: 'pointer' }} src={avatar.src} size={30} />);
        });
    }

    deleteImageAvatar(e) {
        var array = this.state.imageSrc;
        var index = this.state.imageSrc.indexOf({ src: e.currentTarget.dataset.id });
        array.splice(index, 1);
        this.setState({ imageSrc: array });
    }

    render() {
        return (
            <Paper className='wrapper' zDepth={2}>
                <Dialog

                    modal={true}
                    open={this.state.openRegisForm}
                    onRequestClose={this.handleCloseDialog}
                >
                    {
                        this.state.displayRegis == true ? <RegisterForm openLoginForm={this.openLoginForm} handleCloseDialog={this.handleCloseDialog} /> :
                            <LoginForm openRegisForm={this.openRegisForm} handleCloseDialog={this.handleCloseDialog} />
                    }

                </Dialog>

                <form className='regisForm'   >
                    <TextField

                        onChange={this.handleContentChange}
                        fullWidth={true}
                        errorText={this.state.errorText}
                        floatingLabelText={this.state.helpText}
                        hintText="Tất cả bài đăng yêu cầu ít nhất 1 hashtag :)"
                        multiLine={true}
                        rows={1}
                        value={this.state.content}

                        className='content'
                    /><br />
                    <div  >


                    </div>
                    <div className='btnWrapper'   >
                        <Send style={{ cursor: 'pointer' }} onClick={() => { this.inputFileElement.click(); }} />

                        <input style={{ position: 'absolute', top: -1000, width: '1%' }} ref={input => this.inputFileElement = input} type="file" onChange={this.handleUploadImage} className='uploadInput' />

                        <RaisedButton
                            className='submitBtn'
                            backgroundColor="#00bcd4"
                            labelColor="#fff"
                            onClick={this.submitForm} label="Đăng" fullWidth={false} />

                    </div>


                    <Card
                        style={this.state.imageSrc != null ? styles.show : styles.hide}
                    >
                        <CardActions>
                            {this.renderImageAvatar()}
                            <br />
                            {this.state.youtubePreview != null ? <iframe style={{ width: '100%' }} height="315" src={this.state.youtubePreview} frameBorder="0" allowFullScreen></iframe> : null}
                        </CardActions>
                    </Card>


                </form>
                <Snackbar
                    open={this.state.alert}
                    message={this.state.alertMess} 
                    autoHideDuration={2000}
                    onRequestClose={this.handleRequestCloseSnackBar}
                    contentStyle={styles.snackBarContent}

                />
            </Paper >
        );
    }
}

function mapStateToProps(state) {
    return { auth: state.auth };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({ uploadPost }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(PostDetail));


